import React, { useState, Component } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Form from "react-bootstrap/Form";
import { Row, Col, Button } from "react-bootstrap";
import Table from "react-bootstrap/Table";

const Diet = () => {
  const [age, setAge] = useState();
  const [msg1, setMsg1] = useState();
  const [msg2, setMsg2] = useState();

  function calculateDiet() {
    let msg1 = "";
    let msg2 = "";
    if (1 <= age && age <= 3) {
      msg1 =
        " carbohydrate intake: 1000 | fats(%): 15 | protein(%): 10 | calcium(mg): 700";
      msg2 =
        " carbohydrate intake: 1000 | fats(%): 18 | protein(%): 10 | calcium(mg): 700";
    }
    if (4 <= age && age <= 8) {
      msg1 =
        " carbohydrate intake: 1600 | fats(%): 15 | protein(%): 19 | calcium(mg): 1000";
      msg2 =
        " carbohydrate intake: 1400 | fats(%): 18 | protein(%): 19 | calcium(mg): 1000";
    }
    if (9 <= age && age <= 13) {
      msg1 =
        " carbohydrate intake: 2000 | fats(%): 23 | protein(%): 34 | calcium(mg): 1300";
      msg2 =
        " carbohydrate intake: 1800 | fats(%): 32 | protein(%): 34 | calcium(mg): 1000";
    }
    if (14 <= age && age <= 18) {
      msg1 =
        " carbohydrate intake: 3200 | fats(%): 23 | protein(%): 34 | calcium(mg): 1300";
      msg2 =
        " carbohydrate intake: 2200 | fats(%): 32 | protein(%): 34 | calcium(mg): 1300";
    }
    if (19 <= age && age <= 50) {
      msg1 =
        " carbohydrate intake: 3000 | fats(%): 30 | protein(%): 56 | calcium(mg): 1000";
      msg2 =
        " carbohydrate intake: 2400 | fats(%): 42 | protein(%): 46 | calcium(mg): 1000";
    }
    if (51 <= age && age <= 70) {
      msg1 =
        " carbohydrate intake: 2400 | fats(%): 30 | protein(%): 56 | calcium(mg): 1000";
      msg2 =
        " carbohydrate intake: 2200 | fats(%): 42 | protein(%): 46 | calcium(mg): 1200";
    }
    if (71 <= age && age <= 90) {
      msg1 =
        " carbohydrate intake: 2800 | fats(%): 30 | protein(%): 56 | calcium(mg): 1200";
      msg2 =
        " carbohydrate intake: 2200 | fats(%): 42 | protein(%): 46 | calcium(mg): 1200";
    }
    setMsg1(msg1);
    setMsg2(msg2);
  }

  return (
    <React.Fragment>
      <div className="m-5">
        <Form>
          <fieldset>
            <legend>Diet</legend>

            <Form.Row>
              <Col xs={3} controlId="formGridWeight">
                <Form.Label>Age</Form.Label>
                <Form.Control
                  type="number"
                  value={age}
                  onChange={(e) => setAge(+e.target.value)}
                  min="0"
                  placeholder="Enter age"
                />
              </Col>
            </Form.Row>
            <br />
            <Button onClick={calculateDiet} variant="primary">
              Calculate
            </Button>
          </fieldset>
        </Form>
        <br />
        <span>
          <h4>
            <b>Male:</b>
            {msg1}
          </h4>
        </span>
        <span>
          <h4>
            <b>Female:</b>
            {msg2}
          </h4>
        </span>
      </div>
      <br />
    </React.Fragment>
  );
};

export default Diet;
