import React, { Component } from "react";

const Home = () => {
  return (
    <React.Fragment>
      <div className="m-3">
        <h2>Common Problems Faced by the Elderly</h2>
        <hr />
        <p>
          Many people look forward to retirement, viewing it as a time to
          finally relax, slow down, and peacefully enjoy life. While it’s true
          that the golden years can be some of the best years of life, there are
          still some significant challenges - greying hair, wrinkles, forgetting
          where you parked the car. All jokes aside, aging can bring about
          unique health issues. With seniors accounting for 12 percent of the
          world’s population­–and rapidly increasing to over 22 percent by
          2050–it’s important to understand the challenges faced by people as
          they age, and recognize that there are preventive measures that can
          place yourself (or a loved one) on a path to healthy aging.
        </p>
        <hr />
        <br />
        <section id="header" className="d-flex align-items-center">
          <div className="container-fluid nav_bg">
            <div className="row">
              <div className="col-10 mx-auto">
                <div className="row">
                  <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                    <h3
                      style={{
                        color: "",
                        textAlign: "center",
                      }}
                    >
                      <strong className="brand-name">
                        Chronic health conditions
                      </strong>
                    </h3>
                    <p className="my-3">
                      According to the{" "}
                      <a href="https://vitalrecord.tamhsc.edu/10-common-elderly-health-issues/">
                        National Council on Aging
                      </a>
                      , about 92 percent of seniors have at least one chronic
                      disease and 77 percent have at least two. Heart disease,
                      stroke, cancer, and diabetes are among the most common and
                      costly chronic health conditions causing two-thirds of
                      deaths each year. The National Center for Chronic Disease
                      Prevention and Health Promotion recommends meeting with a
                      physician for an annual checkup,{" "}
                      <em style={{ color: "#0000a0" }}>
                        maintaining a healthy diet and keeping an exercise
                        routine
                      </em>{" "}
                      to help manage or prevent chronic diseases. Obesity is a
                      growing problem among older adults and{" "}
                      <em style={{ color: "#0000a0" }}>
                        engaging in these lifestyle behaviors can help reduce
                        obesity
                      </em>{" "}
                      and associated chronic conditions.
                    </p>
                  </div>
                  <div className="col-lg-6 order-1 order-lg-2 header-img">
                    <img
                      src="http://vitalrecord.tamhsc.edu/wp-content/uploads/2016/02/common-elderly-health-issues-chronic-health-conditions.jpg"
                      className="img-fluid animated"
                      alt="home img"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <br />
        <br />
        <section id="header" className="d-flex align-items-center">
          <div className="container-fluid nav_bg">
            <div className="row">
              <div className="col-10 mx-auto">
                <div className="row">
                  <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                    <h3
                      style={{
                        color: "",
                        textAlign: "center",
                      }}
                    >
                      <strong className="brand-name">Cognitive health</strong>
                    </h3>
                    <p className="my-3">
                      Cognitive health is focused on a person’s ability to
                      think, learn and remember. The most common cognitive
                      health issue facing the elderly is
                      <a href="https://www.nia.nih.gov/health/what-dementia-symptoms-types-and-diagnosis">
                        {" "}
                        dementia
                      </a>
                      , the loss of those cognitive functions. Approximately
                      47.5 million people worldwide have dementia—a number that
                      is predicted to nearly triple in size by 2050. The most
                      common form of dementia is Alzheimer’s disease with as
                      many as five million people over the age of 65 suffering
                      from the disease in the United States. According to the{" "}
                      <a href="https://www.nia.nih.gov/">
                        National Institute on Aging
                      </a>
                      , other chronic health conditions and diseases increase
                      the risk of developing dementia, such as substance abuse,
                      diabetes, hypertension, depression, HIV and smoking. While
                      there are no cures for dementia, physicians can prescribe
                      a{" "}
                      <a href="https://www.nia.nih.gov/alzheimers/publication/dementias/treatment">
                        treatment plan
                      </a>{" "}
                      and medications to manage the disease.
                    </p>
                  </div>
                  <div className="col-lg-6 order-1 order-lg-2 header-img">
                    <img
                      src="http://vitalrecord.tamhsc.edu/wp-content/uploads/2016/02/common-elderly-health-issues-dementia.jpg"
                      className="img-fluid animated"
                      alt="home img"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <br />
        <br />

        <section id="header" className="d-flex align-items-center">
          <div className="container-fluid nav_bg">
            <div className="row">
              <div className="col-10 mx-auto">
                <div className="row">
                  <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                    <h3
                      style={{
                        color: "",
                        textAlign: "center",
                      }}
                    >
                      <strong className="brand-name">Mental health</strong>
                    </h3>
                    <p className="my-3">
                      According to the World Health Organization,{" "}
                      <em style={{ color: "#0000a0" }}>
                        over 15 percent of adults over the age of 60 suffer from
                        a mental disorder
                      </em>
                      . A common mental disorder among seniors is depression,
                      occurring in seven percent of the elderly population.
                      Unfortunately, this mental disorder is often
                      underdiagnosed and undertreated. Older adults account for{" "}
                      <em style={{ color: "#0000a0" }}>
                        over 18 percent of suicides deaths
                      </em>{" "}
                      in the United States. Because depression can be a side
                      effect of chronic health conditions, managing those
                      conditions help. Additionally, promoting a lifestyle of
                      healthy living such as betterment of living conditions and
                      social support from family, friends or support groups can
                      help treat depression.
                    </p>
                  </div>
                  <div className="col-lg-6 order-1 order-lg-2 header-img">
                    <img
                      src="http://vitalrecord.tamhsc.edu/wp-content/uploads/2016/02/common-elderly-health-issues-depression.jpg"
                      className="img-fluid animated"
                      alt="home img"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        <br />
        <br />
        <br />

        <section id="header" className="d-flex align-items-center">
          <div className="container-fluid nav_bg">
            <div className="row">
              <div className="col-10 mx-auto">
                <div className="row">
                  <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                    <h3
                      style={{
                        color: "",
                        textAlign: "center",
                      }}
                    >
                      <strong className="brand-name">Physical injury</strong>
                    </h3>
                    <p className="my-3">
                      Every 15 seconds, an older adult is admitted to the
                      emergency room for a fall.{" "}
                      <em style={{ color: "#0000a0" }}>
                        A senior dies from falling every 29 minutes
                      </em>
                      , making it the leading cause of injury among the elderly.
                      Because aging causes bones to shrink and muscle to lose
                      strength and flexibility, seniors are more susceptible to
                      losing their balance, bruising and fracturing a bone. Two
                      diseases that contribute to frailty are osteoporosis and
                      osteoarthritis. However, falls are not inevitable. In many
                      cases, they can be prevented through education, increased
                      physical activity and practical modifications within the
                      home.
                    </p>
                  </div>
                  <div className="col-lg-6 order-1 order-lg-2 header-img">
                    <img
                      src="http://vitalrecord.tamhsc.edu/wp-content/uploads/2016/02/common-elderly-health-issues-physical-injury.jpg"
                      className="img-fluid animated"
                      alt="home img"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <br />
      <hr />
    </React.Fragment>
  );
};

export default Home;
