import React, { useState, Component } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Form from "react-bootstrap/Form";
import { Row, Col, Button } from "react-bootstrap";
import Table from "react-bootstrap/Table";

const Bmi = () => {
  const [number1, setNumber1] = useState();
  const [number2, setNumber2] = useState();
  const [bmi, setBmi] = useState(number1 / (number2 * number2));
  const [msg, setMsg] = useState();

  function calculateBmi() {
    const bmi = number1 / (number2 * number2);
    let msg = "";
    setBmi(bmi);
    if (bmi < 25) {
      if (bmi < 18.5) {
        msg = "underweight";
      } else {
        msg = "ideal";
      }
    } else {
      if (bmi < 30) {
        msg = "overweight";
      } else {
        msg = "obesity";
      }
    }
    setMsg(msg);
  }

  return (
    <React.Fragment>
      <div className="m-2">
        <br />
        <Form>
          <fieldset>
            <legend>BMI calculation</legend>

            <Form.Row>
              <Col xs={3} controlId="formGridWeight">
                <Form.Label>Weight</Form.Label>
                <Form.Control
                  type="number"
                  value={number1}
                  onChange={(e) => setNumber1(+e.target.value)}
                  min="0"
                  placeholder="Enter weight in kilograms"
                />
              </Col>

              <Col xs={3} controlId="formGridHeight">
                <Form.Label>Height</Form.Label>
                <Form.Control
                  type="number"
                  value={number2}
                  onChange={(e) => setNumber2(+e.target.value)}
                  placeholder="Enter height in meter"
                  min="0"
                />
              </Col>
            </Form.Row>
            <br />
            <Button onClick={calculateBmi} variant="primary">
              Calculate
            </Button>
          </fieldset>
        </Form>
        <br />
        <h4 className="m-2">BMI : {bmi}</h4>
        <h4>{msg}</h4>
      </div>
      <br />
      <p className="m-3">
        <h3>
          According to <b>National Institutes of Health (NIH)</b>:
        </h3>
        <ul className="m-2">
          <li>BMI less than 18.5 means that a person is underweight</li>
          <li>BMI between 18.5 and 24.9 is ideal</li>
          <li>BMI between 25 and 29.9 is overweight</li>
          <li>BMI over 30 indicates obesity</li>
        </ul>
      </p>
      <br />
      <br />
    </React.Fragment>
  );
};

export default Bmi;
